import { get } from "svelte/store";
import { rawWidth } from "./state/AppState";
import ATLSmall from "$lib/images/atl-small.jpg";
import ATLLarge from "$lib/images/atl-large.jpg";
import CartaLarge from "$lib/images/carta-large.jpg";
import CartaSmall from "$lib/images/carta-small.jpg";
import WordCloudsSmall from "$lib/images/wordClouds-small.png";
import WordCloudsLarge from "$lib/images/wordClouds.png";
import SkedgeSmall from "$lib/images/skedge-small.jpg";
import SkedgeLarge from "$lib/images/skedge-desktop2.jpg";
import ReactSmall from "$lib/images/react-small.jpg";
import ReactLarge from "$lib/images/react-desktop2.jpg";
import SVSmall from "$lib/images/sv-small.jpg";
import SVLarge from "$lib/images/sv-large.jpg";
import CommerceSmall from "$lib/images/gnocchi-small.jpg";
import CommerceLarge from "$lib/images/gnocchi.jpg";
import EatSmall from "$lib/images/republic-small.jpg";
import EatLarge from "$lib/images/republic-large.jpg";

export class Preloader {
  public static initialize() {
    const loaded = [];
    const imgs = get(rawWidth) >= 670 ? this.largeImages : this.smallImages;
    for (let i = 0; i < imgs.length; i++) {
      const img = new Image();
      img.src = imgs[i];
      loaded[i] = img;
    }
  }

  private static readonly smallImages = [
    ATLSmall,
    CartaSmall,
    WordCloudsSmall,
    SkedgeSmall,
    ReactSmall,
    SVSmall,
    CommerceSmall,
    EatSmall,
  ];

  private static readonly largeImages = [
    ATLLarge,
    CartaLarge,
    WordCloudsLarge,
    SkedgeLarge,
    ReactLarge,
    SVLarge,
    CommerceLarge,
    EatLarge,
  ];
}
